<template>
  <div class="row">
    <div v-if="!isReady" class="loading-screen bg-white flex-center">
      <div class="snippet" data-title=".dot-flashing">
        <div class="stage">
          <div class="dot-flashing"></div>
        </div>
      </div>
    </div>

    <div
      v-if="orders.length == 0"
      class="col-12 vh-100 flex-column flex-center"
    >
      <div
        class="flex-x-center w-100"
        style="
          padding: 0px 10px;
          margin-top: 30px;
          position: fixed;
          top: 0;
          z-index: 100;
        "
      >
        <!-- <h4 class="mb-0">Yiu's Villa Supplier</h4> -->
        <img src="/logo.png" width="150px" alt="" />
      </div>
      <input
        placeholder="Enter your phone number"
        type="number"
        id="phone-number"
        v-model="phone"
      />
      <button @click="startToGet" class="search-my-orders">
        <span v-if="!getting"> Confirm </span>
        <div class="snippet" v-else data-title=".dot-flashing">
          <div class="stage">
            <div class="dot-flashing"></div>
          </div>
        </div>
      </button>
    </div>
    <div v-else class="col-12 px-0">
      <div
        class="flex-x-center w-100"
        style="padding: 0px 10px; margin-top: 30px"
      >
        <!-- <h4 class="mb-0">Yiu's Villa Supplier</h4> -->
        <img src="/logo.png" width="150px" alt="" />
      </div>
      <div class="card-product mb-3">
        <div class="flex-x-between">
          <h5 style="margin: 15px 0px">Customer</h5>
        </div>
        <p class="gray-bg">Name : {{ orders[0].customer_name }}</p>
        <p class="gray-bg">
          Phone :
          {{ phone }}
        </p>
        <p class="gray-bg" v-if="orders[0].address">
          Address :{{ orders[0].address.address }},
          {{ orders[0].address.city.split("_").join(" ") }},
          {{ orders[0].address.state.split("_").join(" ") }}
        </p>
      </div>
      <div class="card-product">
        <h5 style="margin: 15px 0px">Orders</h5>
        <div
          class="gray-bg"
          style="position: relative"
          v-for="(order, index) in orders"
          :key="index"
          @click="getToDetail(order)"
        >
          <p class="my-1">Order ID : {{ order.sale_no }}</p>
          <p class="my-1">Date : {{ order.date }}</p>
          <p class="my-1">
            Total :
            {{ parseInt(order.total) * parseInt(order.money_price) }} MMK
          </p>
        </div>
      </div>
      <button @click="clearPhone" class="danger-button">
        <span> This is not my orders! </span>
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      orders: [],
      phone: "",
      getting: false,
      isReady: false,
    };
  },
  created() {
    const phone = localStorage.getItem("phone");
    if (phone) {
      this.phone = phone;
      this.getOrders(phone);
    } else {
      this.isReady = true;
      const loader = document.querySelector(".page-loader");
      loader.className += " hidden";
      setTimeout(() => {
        loader.className += " display_none";
      }, 1000);
    }
  },
  components: {},
  methods: {
    startToGet() {
      localStorage.setItem("phone", this.phone);
      this.getOrders(this.phone);
      this.getting = true;
    },
    clearPhone() {
      localStorage.removeItem("phone");
      this.orders = [];
    },
    getToDetail(order) {
      this.$router.push({
        name: "Home",
        query: { sid: "3", oid: order.id, date: order.date },
      });
    },
    getOrders(phone) {
      var link = `https://osassist.pythonanywhere.com/api/my-sales/?phone=${phone}&sid=3`;
      axios
        .get(link)
        .then((response) => {
          this.orders = response.data;
          this.getting = false;
          this.isReady = true;
          const loader = document.querySelector(".page-loader");
          loader.className += " hidden";
          setTimeout(() => {
            loader.className += " display_none";
          }, 1000);
        })
        .catch(() => {
          this.getting = false;
          alert("Error!");
        });
    },
  },
};
</script>

<style>
input#phone-number {
  border-radius: 8px;
  border: 2px solid #6951a4;
  padding: 10px;
  width: 100%;
  margin: 15px;
  font-size: 20px;
}
.danger-button {
  margin: 10px 0px !important;
  background: rgb(255 0 0 / 15%);
  padding: 5px 8px;
  border-radius: 10px;
  color: #cb0000;
  height: 45px;
  width: 100%;
  font-size: 16px;
  border: 0px;
}
button.search-my-orders:active {
  transform: scale(0.9);
}
button.search-my-orders {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #6951a4;
  border-radius: 8px;
  padding: 8px;
  transition: all 0.3s ease-in-out;
  width: 100%;
  font-size: 20px;
  color: #fff;
  border: 0px;
  height: 45px;
}
</style>